	module.exports = {"data":{"markdownRemark":{"html":"<p>To clone a branch, enter the following into the terminal:  </p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>git clone -b <branch> <remote_repo></code></pre>\n      </div>\n<p>If you only want to fetch the specified branch:  </p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>git clone -b <branch> --single-branch <remote_repo></code></pre>\n      </div>\n<p>Here is an example of fetching the <code>staging</code> branch from FreeCodeCamp:  </p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>git clone -b staging https://github.com/FreeCodeCamp/FreeCodeCamp.git</code></pre>\n      </div>","fields":{"slug":"/miscellaneous/cloning-a-specific-branch-in-git/"},"frontmatter":{"title":"Cloning a Specific Branch in Git"}}},"pathContext":{"slug":"/miscellaneous/cloning-a-specific-branch-in-git/"}}

